import React from 'react';
import { useState, useEffect } from 'react';
import { NotionRenderer } from 'react-notion';

import { Link } from "react-router-dom";
import { PageLink, PageWrapper } from "constants/Mixins";

// For styling markdown content
import 'react-notion/src/styles.css';
import './writing.css';

export default function Loop() {
  const [data, setData] = useState({});

  useEffect(() => {
    document.title = "lessons from my internship at loop - writing";
  }, []);

  useEffect(() => {
    fetch(
      'https://notion-api.splitbee.io/v1/page/bb01ab5824864d64adb27cc916985628'
    )
      .then((res) => {
        if (!res.ok) {
          throw new Error(res.statusText)
        }
        return res.json();
      })
      .then((data) => setData(data))
      .catch(err => {
        console.log(err)
      });
  }, []);

  return (
    <PageWrapper className='writing'>
      <div className="writing">
        <Link to="/writing">
          <PageLink>&#8592; Writing</PageLink>
        </Link>
        <h1>lessons from my internship at loop</h1>
        <h4>a gap semester well spent</h4>
        <p>2023-12-12</p>
        <p>coming soon...</p>
        <NotionRenderer blockMap={data} />
      </div>
    </PageWrapper>

  );
}
